import How1 from "@/assets/img/solution/how1.png";
import How2 from "@/assets/img/solution/how2.png";
import How3 from "@/assets/img/solution/how3.png";

export default {
  name: "How",
  components: {
    item: Item,
  },
  data() {
    var self = this;

    return {
      isNextIcon: true,
      swiperOption: {
        notNextTick: true,
        initialSlide: 1,
        spaceBetween: 0,
        slidesPerView: 1,
        loop: false,
        direction: "horizontal",
        grabCursor: false,
        setWrapperSize: true,
        autoHeight: false,
        mousewheelControl: true,
        observeParents: true,
        debugger: true,
        paginationClickable: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },

        on: {
          slideChangeTransitionEnd: function () {
            self.setNextIcon(this.realIndex != 2);
          },
        },
      },
      how1: How1,
      how2: How2,
      how3: How3,
      data: [
        {
          title: "協助孵化",
          content: "廠商提供呈現內容、理念、成員",
          icarry: ["基本行銷建議", "美工設計", "網站前台/管理後台開發"],
          img: How1,
        },
        {
          title: "協同育成",
          content: "廠商業績提升，客戶成長， 產生功能、流量轉換需求",
          icarry: ["時數合約-", "功能擴充/架構擴充", "行銷工具導入- GA/SEO"],
