          content: "廠商已有內部專業行銷團隊",
          icarry: ["時數合約-", "功能修改/擴充"],
          img: How3,
        },
      ],
    };
  },
  computed: {
    sliderhowSwiper() {
      return this.$refs.mySwiper.$swiper;
    },
  },
  methods: {
    prevClick() {
      this.sliderhowSwiper.slidePrev();
    },
    nextClick() {
      if (this.sliderhowSwiper.realIndex == 2) {
        this.sliderhowSwiper.slideToLoop(0);
      } else {
        this.sliderhowSwiper.slideNext();
      }
    },
    setNextIcon(hasNext) {
      console.log(this.isNextIcon);

      this.isNextIcon = hasNext;
    },
  },
};
